.board-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.topic-container {
  flex-direction: column;
  padding: 10px;
  max-width: 128px;
}

.topic-header {
  display: flex;
  font-size: 12px;
  text-align: center;
  align-items: center;
  justify-content: center;
  width: 128px;
  height:128px;
}

.question-box {
  display: flex;
  font-size: 12px;
  text-align: center;
  align-items: center;
  justify-content: space-evenly;
  width: 128px;
}

.item-box, .item-box-solved {
  display: flex;
  align-items: center;
  justify-content: center;

  margin-bottom: 10px
}

.item-box {
  background-color:aquamarine;
}

.item-box-solved {
  background-color: red;
}

.board-button {
  height: 64px;
  width: 128px;

  font-size: 16px;
  font-weight:900;
  border: 1px solid yellow;
  background-color: darkblue;
  color: gold;
}

.board-button:hover {
  height: 64px;
  width: 128px;

  font-size: 16px;
  font-weight:900;
  border: 1px solid yellow;
  background-color: blue;
  color: yellow;
}

.board-button-solved {
  height: 64px;
  width: 128px;

  font-size: 16px;
  border: none;
  background-color: darkblue;
  color: darkblue;
}

.question-answer {
  display: flex; 
  flex-direction: column;
  align-items: center;
  justify-content: center;

  margin-top: 20%;
  font-size: 30px; 
}

.progress-button {
  height: 64px; 
  width: 256px;

  font-size: 16px;

  font-size: 16px;
  font-weight:900;
  border: 1px solid yellow;
  background-color: darkblue;
  color: gold;
}

.progress-button:hover {
  height: 64px; 
  width: 256px;

  font-size: 16px;

  font-size: 16px;
  font-weight:900;
  border: 1px solid yellow;
  background-color: blue;
  color: yellow;
}

